.btn-light {
  color: #d41443;
  height: 44px;
  cursor: pointer;
  background-color: #fff;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-light:hover, .btn-light:focus {
  background-color: #ecebeb;
}

.btn-dark {
  color: #fff;
  height: 44px;
  cursor: pointer;
  background-color: #d41443;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
