}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop .nav-list {
  flex-direction: column;
  margin-top: 19px;
  margin-left: 21px;
  display: flex;
}

